import React from 'react'
import { Route, NavLink, Switch, useRouteMatch } from 'react-router-dom'
import Lessons from './Lessons'
import Certification from './services/CoachCertification'
import BankSystem from './services/BankSystem'
import OnlineService from './services/OnlineService'

function Services() {
  const { path, url } = useRouteMatch()

  return (
    <div>
      <div className="banner">
        <h2 className="text-4xl md:text-5xl banner__title"></h2>
        <div className="hidden md:block banner__desc"></div>
      </div>
      <div className="container mx-auto nav-menu">
        <div className="nav-menu-item">
          <NavLink className="nav-menu-item__txt" activeClassName="is-active" to={`${url}/certification`}></NavLink>
        </div>
        <div className="nav-menu-item">
          <NavLink className="nav-menu-item__txt" activeClassName="is-active" to={url} exact></NavLink>
        </div>
        <div className="nav-menu-item">
          <NavLink className="nav-menu-item__txt" activeClassName="is-active" to={`${url}/bank`}></NavLink>
        </div>
        <div className="nav-menu-item">
          <NavLink className="nav-menu-item__txt" activeClassName="is-active" to={`${url}/online`}></NavLink>
        </div>
      </div>
      <Switch>
        <Route path={path} exact><Lessons /></Route>
        <Route path={`${path}/certification`}><Certification /></Route>
        <Route path={`${path}/bank`}><BankSystem /></Route>
        <Route path={`${path}/online`}><OnlineService /></Route>
      </Switch>
    </div>
  )
}

export default Services